import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

export type ButtonHierarchy =
  | 'primary'
  | 'secondary'
  | 'tertiary'
  | 'quaternary';
export type ButtonIconPosition = 'none' | 'leading' | 'trailing' | 'only';
export type ButtonSize = 'xs' | 'sm' | 'md' | 'lg';
export type ButtonKind = 'default' | 'destructive' | 'success';
export type ButtonType = 'button' | 'submit' | 'reset';

@Component({
  tag: 'bh-button',
  styleUrl: 'bh-button.css',
  shadow: true,
})
export class BhButton {
  /**
   * The visual hierarchy of the button
   */
  @Prop() hierarchy: ButtonHierarchy = 'primary';

  /**
   * The kind/semantic meaning of the button
   */
  @Prop() kind: ButtonKind = 'default';

  /**
   * Icon position relative to the label
   */
  @Prop() icon: ButtonIconPosition = 'none';

  /**
   * The size of the button
   */
  @Prop() size: ButtonSize = 'md';

  /**
   * Fallback text label (used only if no slot content is provided)
   */
  @Prop() label?: string;

  /**
   * Material Symbols icon name (e.g., 'add', 'edit', 'delete')
   */
  @Prop() iconName?: string;

  /**
   * Whether the button is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Whether the button is in a loading state
   */
  @Prop() loading: boolean = false;

  /**
   * The type of button for form submission
   * - 'button': No form submission (default)
   * - 'submit': Submits the form
   * - 'reset': Resets the form
   */
  @Prop() type: ButtonType = 'button';

  /**
   * Emitted when the button is clicked
   */
  @Event() bhClick!: EventEmitter<MouseEvent>;

  private handleClick = (event: MouseEvent): void => {
    if (!this.disabled && !this.loading) {
      this.bhClick.emit(event);
    }
  };

  private getIconSizeClass(): string {
    switch (this.size) {
      case 'sm':
      case 'md':
        return 'icon-md';
      case 'lg':
        return 'icon-lg';
      default:
        return 'icon-md';
    }
  }

  private getTextSizeClass(): string {
    switch (this.size) {
      case 'sm':
      case 'md':
        return 'text-sm';
      case 'lg':
        return 'text-md';
      default:
        return 'text-sm';
    }
  }

  private getLoaderSize(): 'sm' | 'md' {
    switch (this.size) {
      case 'xs':
      case 'sm':
        return 'sm';
      default:
        return 'sm';
    }
  }

  private getLoaderVariant(): 'default' | 'white' {
    // Primary buttons (all kinds) have dark background, so use white spinner
    return this.hierarchy === 'primary' ? 'white' : 'default';
  }

  render() {
    const showLabel = this.icon !== 'only';
    const showLeadingIcon =
      this.icon === 'leading' && (this.iconName || this.loading);
    const showTrailingIcon =
      this.icon === 'trailing' && this.iconName && !this.loading;
    const showOnlyIcon =
      this.icon === 'only' && (this.iconName || this.loading);

    const buttonClasses = {
      btn: true,
      [`btn-${this.hierarchy}`]: true,
      [`btn-${this.kind}`]: true,
      [`btn-${this.size}`]: true,
      [`btn-icon-${this.icon}`]: true,
      'btn-disabled': this.disabled,
      'btn-loading': this.loading,
    };

    return (
      <button
        type={this.type}
        class={buttonClasses}
        disabled={this.disabled}
        onClick={this.handleClick}
        part="button"
      >
        {showLeadingIcon && (
          <span class={`btn-icon ${this.getIconSizeClass()}`}>
            {this.loading ? (
              <bh-loader-spinner
                size={this.getLoaderSize()}
                variant={this.getLoaderVariant()}
              ></bh-loader-spinner>
            ) : (
              <span class="btn-icon-material material-symbols-outlined">
                {this.iconName}
              </span>
            )}
          </span>
        )}

        {showOnlyIcon && (
          <span class={`btn-icon ${this.getIconSizeClass()}`}>
            {this.loading ? (
              <bh-loader-spinner
                size={this.getLoaderSize()}
                variant={this.getLoaderVariant()}
              ></bh-loader-spinner>
            ) : (
              <span class="btn-icon-material material-symbols-outlined">
                {this.iconName}
              </span>
            )}
          </span>
        )}

        {showLabel && (
          <span class={`btn-label ${this.getTextSizeClass()}`}>
            <slot>{this.label}</slot>
          </span>
        )}

        {showTrailingIcon && (
          <span class={`btn-icon ${this.getIconSizeClass()}`}>
            <span class="btn-icon-material material-symbols-outlined">
              {this.iconName}
            </span>
          </span>
        )}
      </button>
    );
  }
}
